<template>
  <div class="mask note-menu" v-on:click="onClickMask">
    <div v-bind:style="style" class="my-remove-tip" id="markpannel">
      <input
        v-bind:enable="UnderlineEnable"
        type="button"
        value="下划线"
        v-bind:class="ulstyle"
        v-on:click="onUnderline"
        v-bind:style="ulstyle"
      />
      <div class="d1 hlyellow right" v-on:click="onYellow">
        <span v-if="isYellow">&#10004;</span>
      </div>
      <div class="d1 hlred right" v-on:click="onRed">
        <span v-if="isRed">&#10004;</span>
      </div>
      <div class="d1 hlgreen right" v-on:click="onGreen">
        <span v-if="isGreen">&#10004;</span>
      </div>
      <input type="button" class="right icon-del" v-on:click="onDelete" />
      <input
        type="button"
        value="复制"
        class="round_button right"
        v-on:click="onCopy"
      />
    </div>
  </div>
</template>






<script>
import {yellow,green,red,ul} from "../hl_mengshou"
export default {
  name: "NoteMenu",
  data() {
    return {
      style: {
        left: this.left - 20 + "px",
        top: this.top - 25 - window.pageYOffset + "px",
        width: 6 * 30,
      },
    };
  },
  computed: {
    ulstyle() {
      let ret = "round_button right ";
      if (this.UnderlineEnable) {
        return ret + " enalbe_ul_btn";
      } else {
        return ret + " disable_ul_btn";
      }
    },
    isYellow() {
      return this.color == yellow;
    },
    isRed() {
      return this.color == red;
    },
    isGreen() {
      return this.color == green;
    },
  },
  methods: {
    onUnderline(e) {
      this.UnderlineEnable = this.UnderlineEnable == false;
      if(this.UnderlineEnable){
        this.onClick(e, ul);
      }
      else{ 
        this.color = red;
        this.onClick(e, red);
      }
    },
    onSearch() {
      console.log("xx");
      this.removeSelectionHighLight();
      this.removeMenu();
    },
    onCopy() {
      console.log("onCopy");
      let { hl } = window;
      hl.onCopy();
      this.removeSelectionHighLight();
      this.removeMenu();
    },
    // eslint-disable-next-line no-unused-vars
    removeSelectionHighLight() {
      if (this.color == undefined) {
        if (this.onClose) {
          this.onClose(undefined, this.noteid);
        }
      }
    },
    onClickMask(e) {
      let mask = document.getElementsByClassName("note-menu")[0];
      if (mask != e.target) return;
      this.removeSelectionHighLight();
      this.removeMenu();
    },
    onClick(e, color) {
      this.color = color;
      this.UnderlineEnable = color==ul;
      if (this.onClose) {
        this.onClose(color, this.noteid);
      } else {
        let { hl } = window;
        hl.setHighlightColor(color, this.noteid);
      }
    },
    onRed(e) {
      this.onClick(e, red);
    },
    onGreen(e) {
      this.onClick(e, green);
    },
    onYellow(e) {
      this.onClick(e, yellow);
    },
    removeMenu() {
      var tips = document.getElementsByClassName("note-menu");
      tips.forEach((tip) => {
        tip.parentNode.removeChild(tip);
      });
    },
    onDelete() {
      const id = this.noteid;
      let { hl } = window;
      console.log("*click remove-tip*", id);
      hl.deleteId(id);
      this.removeMenu();
    },
  },
  props: {
    color: {
      type: Number,
      default: undefined,
    },
    left: {
      type: Number,
      default: undefined,
    },
    onClose: {
      type: Function,
      default: undefined,
    },
    top: {
      type: Number,
      default: undefined,
    },
    noteid: {
      type: String,
      default: undefined,
    },
    nochoose: {
      type: Boolean,
      default: true,
    },
    UnderlineEnable: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
<style>
@import "../assets/web.css";
</style>
<style type="text/css">
.icon-del {
  background-image: url(../assets/del.svg);
  background-size: 16px 16px;
  background-position: 2px 2px;
  background-repeat: no-repeat;
  padding: 2px 2px 2px 2px;
  border: 1px solid #ddd;
  margin: 4px;
  width: 30px;
  border-radius: 3px;
  text-align: center;
  height: 22px;
}
.my-remove-tip {
  box-sizing: border-box;
  position: absolute;
  border: 1px solid #fff;
  border-radius: 3px;
  height: 30px;
  /* width        : 90px; */
  color: #fff;
  background: rgb(226, 223, 223);
  /* text-align   : center; */
  font-size: 12px;
  cursor: pointer;
  line-height: 18px;
  overflow: visible;
}
.disable_ul_btn {
  border: gray;
  color: gray;
  text-decoration: underline;
}
.enalbe_ul_btn {
  border:red;
  color:red;
  text-decoration: underline;
}

</style>